<template>
   <div>
       <v-dialog
            max-width="400"
            persistent
            v-model="soldDialog"
        >
            <template>
                <v-card class="ip_add">

                    <div class="ip_add__title">
                        <span class="text-black">Mahsulot sotilmoqda</span>
                        <v-btn icon @click="closeDialog()">
                            <img src="@/assets/images/BackButton.svg" alt="BackButton">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formSellProduct" v-slot="{ handleSubmit, invalid }">

                        <form class="ip_add__content" @submit.prevent="handleSubmit(onSubmit)">
                    
                            <div class="mt-3">
                                <span class="text-black ml-1">Sana</span>
                                
                                    <v-menu
                                        v-model="dateMenu"
                                        :close-on-content-click="false"
                                        :nudge-right="40"
                                        transition="scale-transition"
                                        offset-y
                                        min-width="auto"
                                    >
                                        <template v-slot:activator="{ on, attrs }">
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <v-text-field
                                                    class="mt-2"
                                                    placeholder="Tanlang"
                                                    v-model="dateFormatted"
                                                    readonly
                                                    outlined
                                                    v-bind="attrs"
                                                    v-on="on"
                                                    @blur="date = parseDate(dateFormatted)"
                                                    :error-messages="errors"
                                                ></v-text-field>
                                            </ValidationProvider>
                                        </template>
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                        <v-date-picker
                                            color="blue lighten-1"
                                            v-model="date"
                                            @input="dateMenu = false"
                                            locale="ru-ru"
                                            first-day-of-week="1"
                                            :error-messages="errors"
                                        ></v-date-picker>
                                        </ValidationProvider>
                                    </v-menu>
                            </div>

                            <div>
                                <!-- <span class="text-black ml-1">Kimga sotildi</span>
                                <ValidationProvider :rules="{required: true, 
                                error: [errorStatus, errorMessage, errorPlace, 'customer']}"
                                 v-slot="{ errors }">
                                    <v-text-field class="mt-2" outlined
                                        v-model="sold.customer" :error-messages="errors"
                                        placeholder="Misol: NEON textile">
                                    </v-text-field>
                                </ValidationProvider> -->
                            


                                <span class="text-black ml-1">Kimga sotildi</span>
                                <v-list class="d-flex">
                                    <div class="radioContent">
                                        <span>Mavjud mijoz</span>
                                        <v-radio-group class="radioGroup" v-model="customer_type">
                                            <v-radio value="existing"></v-radio>
                                        </v-radio-group>
                                    </div>
                                    <div class="radioContent">
                                        <span>Yangi mijoz</span>
                                        <v-radio-group class="radioGroup" v-model="customer_type">
                                            <v-radio value="new"></v-radio>
                                        </v-radio-group>
                                    </div>
                                </v-list>


                                <div v-if="customer_type == 'new'" class="mt-4">
                                    <span class="text-black ml-1">Ism va telefon raqami</span>
                                    <ValidationProvider :rules="{required: true, 
                                    error: [errorStatus, errorMessage, errorPlace, 'name']}" v-slot="{ errors }">
                                        <v-text-field 
                                            v-model="sold.customer" class="mt-2" outlined maxlength="100"
                                            placeholder="Ism kiriting" :error-messages="errors">
                                        </v-text-field>
                                    </ValidationProvider>

                                    <ValidationProvider :rules="{required: true,
                                     error: [errorStatus, errorMessage, errorPlace, 'phone_number']}" v-slot="{ errors }">
                                        <v-text-field
                                            :error-messages="errors"
                                            v-model="sold.phone_number" outlined placeholder="Telefon raqam kiriting">
                                        </v-text-field>
                                    </ValidationProvider>
                                </div>
                                
                                
                                <div v-else-if="customer_type == 'existing'" class="mt-3">
                                    <div class="exists">
                                        <span class="exists__title text-black">Mavjud mijozni tanlang</span>
                                        <v-text-field 
                                            v-model="customer_select" outlined class="exists__input">
                                            <template v-slot:prepend-inner>
                                                <img src="@/assets/icons/search-normal.svg" 
                                                class="mb-4 mt-0" alt="search-normal">
                                            </template>
                                        </v-text-field>
                                    </div>

                                    <perfect-scrollbar class="mr-1">
                                        <div class="radioContent mb-5" v-for="(element, index) in existingCustomers" :key="index">
                                            <span>{{ element.name }} | {{element.phone_number}}</span>
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <v-radio-group class="radioGroup" v-model="existingCustomer" :error-messages="errors">
                                                    <v-radio :value="element.id"></v-radio>
                                                </v-radio-group>
                                            </ValidationProvider>
                                        </div>
                                    </perfect-scrollbar>
                                </div>

                            </div>

                            <div class="part_content">

                                <div>
                                    <span class="text-black ml-1">Tovarni tanlang</span>
                                    <v-text-field
                                        @click="productSelectDialog = true"
                                        append-icon="mdi-chevron-right" outlined
                                        :value="existingProduct.product ? existingProduct.product.name : ''"
                                        class="mt-2" placeholder="Tanlang">
                                    </v-text-field>
                                </div>


                                <div>
                                    <span class="text-black ml-1">Adadi (ta)</span>
                                    <ValidationProvider :rules="{required: true, 
                                    error: [errorStatus, errorMessage, errorPlace, 'amount']}"
                                     v-slot="{ errors }">
                                        <v-text-field :error-messages="errors"
                                            @input="formatPrice(sold.amount, 'amount')"
                                            v-model="sold.amount" min="1"
                                            onkeypress="return /[0-9,/.]/i.test(event.key)"
                                            outlined placeholder="Misol: 480 ta" class="mt-2">
                                        </v-text-field>
                                    </ValidationProvider>
                                </div>


                                <div>
                                    <span class="text-black ml-1">Dona narxi</span>
                                    <ValidationProvider :rules="{required: true,
                                     error: [errorStatus, errorMessage, errorPlace, 'price']}" 
                                     v-slot="{ errors }">
                                        <v-text-field :error-messages="errors"
                                            @input="formatPrice(sold.price, 'price')"
                                            v-model="sold.price" min="1"
                                            onkeypress="return /[0-9,/.]/i.test(event.key)"
                                            outlined placeholder="Misol: 180,000 som" class="mt-2">
                                        </v-text-field>
                                    </ValidationProvider>
                                </div>


                                <div>
                                    <span class="text-black ml-1">Umumiy summa</span>
                                    <ValidationProvider :rules="{required: true,
                                     error: [errorStatus, errorMessage, errorPlace, 'total_price']}"
                                      v-slot="{ errors }">
                                        <v-text-field :error-messages="errors"
                                            @input="formatPrice(sold.total_price, 'total_price')"
                                            :value="getTotalPrice"
                                            outlined min="1"
                                            onkeypress="return /[0-9,/.]/i.test(event.key)"
                                            placeholder="Misol: 1,240,000 som"
                                            class="mt-2" v-model="sold.total_price">
                                        </v-text-field>
                                    </ValidationProvider>
                                </div>


                                <div>
                                    <span class="text-black ml-1">Berilgan summa</span>
                                    <ValidationProvider :rules="{required: true, 
                                    error: [errorStatus, errorMessage, errorPlace, 'paid']}" 
                                    v-slot="{ errors }">
                                        <v-text-field :error-messages="errors"
                                            @input="formatPrice(sold.paid, 'paid')"
                                            outlined min="1"
                                            onkeypress="return /[0-9,/.]/i.test(event.key)"
                                            placeholder="Misol: 1,240,000 som"
                                            class="mt-2" v-model="sold.paid">
                                        </v-text-field>
                                    </ValidationProvider>
                                </div>

                            </div>
                        
                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    class="backBtn btn"
                                    elevation="0"
                                    @click="closeDialog()"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn btn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>

                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>



        <v-dialog
            max-width="400"
            persistent
            v-model="productSelectDialog"
        >
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title">
                        <span>Tovarni tanlash</span>
                        <v-btn icon @click="productSelectDialog = false">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formProductSelectReadyProducts" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit2)">

                            <div class="exists mt-5">
                                <span class="exists__title text-black">Bazadagi tovarlar</span>
                                <v-text-field outlined class="exists__input" v-model.lazy="productSearch">
                                    <template v-slot:prepend-inner>
                                        <img src="@/assets/icons/search-normal.svg" class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div>

                            <perfect-scrollbar class="mr-1" @ps-y-reach-end="addItems">
                                <div class="radioContent mb-5" v-for="(element2, index2) in existingProducts" :key="index2">
                                    <span>{{element2.product.name}} - #{{element2.product.code}} - {{element2.amount}} ta</span>
                                    <ValidationProvider 
                                        rules="required"
                                        v-slot="{ errors }">
                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="existingProduct">
                                            <v-radio :value="element2"></v-radio>
                                        </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
   </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    props: ['soldDialog'],
    mixins: [dateMixin, loaderMixin],
    components: {
        PerfectScrollbar
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            page: 1,
            last_page: 2,
            pageCount: 0,

            page2: 1,
            last_page2: 2,
            pageCount2: 0,

            sold:{
                customer: '',
                phone_number: '',
                product_id: '',
                amount: 0,
                price: 0,
                total_price: '',
                paid: 0
            },
            existingProducts: [],
            existingProduct: '',
            productSelectDialog: false,
            productSearch: '',

            existingCustomers: [],
            existingCustomer: '',
            customer_select: '',
            customer_type: 'existing',
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============

        }
    },
    watch: {
        // ============= NEW_IP START =============
        'sold': {
            handler(newValue){
                if(newValue){
                    this.clearErrors()
                }
            }, deep: true,
        },
        // ============= NEW_IP END =============






        // ============= OTHER_WATCHERS START =============
        async productSearch(newValue, oldVal) {
            this.page = 1;
            if(newValue || newValue == ''){
                await this.refresh();
            }
        },
        async customer_select(newValue, oldValue){
            this.page2 = 1;
            if(newValue || newValue == ''){
                await this.refresh2();
            }
        },
        async soldDialog(newValue, oldValue){
            if(newValue){
                await this.refresh();
            }
        },
        // ============= OTHER_WATCHERS END =============


    },
    async mounted(){
        await this.refresh();
        await this.refresh2();
    },
    computed: {
        ...mapState(
            {
                storage_ready_product: state => state.readyProducts.storage_ready_product,
                clients: state => state.readyProducts.clients,
            }
        ),
        // ============= CALCULATE_TOTAL_PRICE START =============
        getTotalPrice() {
            let price = this.sold.price.toString().replace(/,/g, '');
            let amount = this.sold.amount.toString().replace(/,/g, '');
            price = parseFloat(price)
            amount = parseFloat(amount)

            let result = price * amount
            
            return  this.sold.total_price = new Intl.NumberFormat('en-US').format(result)
        }
        // ============= CALCULATE_TOTAL_PRICE END =============
    },
    methods:{
        ...mapActions(
            {
                postSales: 'readyProducts/postSales',
                fetchStorageReadyProduct: 'readyProducts/fetchStorageReadyProduct',
                fetchClients: 'readyProducts/fetchClients',
                postClient: 'readyProducts/postClient'
            }
        ),



        // ============= REFRESH START =============
        async refresh(){
            this.page = 1;
            
            clearTimeout(this._timerId)

            this._timerId = setTimeout(async () => {
                await this.fetchStorageReadyProduct({page: this.page, category_id: this.$route.params.id, search: this.productSearch});
                    
                this.page = this.storage_ready_product.current_page;
                this.last_page = this.storage_ready_product.last_page;
                this.pageCount = Math.ceil(this.storage_ready_product.total / 20);
                this.existingProducts = this.storage_ready_product.data
            }, 500)
        },
        // ============= REFRESH END =============



        // ============= ADD_ITEMS START =============
        async addItems(){
            if(this.page < this.last_page){
                this.page = this.page + 1;

                clearTimeout(this._timerId)

                this._timerId = setTimeout(async () => {
                    await this.fetchStorageReadyProduct({page: this.page, category_id: this.$route.params.id, search: this.productSearch});
                    this.page = this.storage_ready_product.current_page;
                    this.pageCount = Math.ceil(this.storage_ready_product.total / 20);

                    this.existingProducts = this.existingProducts.concat(this.storage_ready_product.data);
                }, 500)
            }
        },
        // ============= ADD_ITEMS END =============









        // ============= REFRESH2 START =============
        async refresh2(){
            this.page2 = 1;
            
            clearTimeout(this._timerId2)

            this._timerId2 = setTimeout(async () => {
                await this.fetchClients({page: this.page2, search: this.customer_select});
                    
                this.page2 = this.clients.current_page;
                this.last_page2 = this.clients.last_page;
                this.pageCount2 = Math.ceil(this.clients.total / 20);
                this.existingCustomers = this.clients.data
            }, 500)
        },
        // ============= REFRESH2 END =============









        // ============= ADD_ITEMS START =============
        async addItems2(){
            if(this.page2 < this.last_page2){
                this.page2 = this.page2 + 1;

                clearTimeout(this._timerId2)

                this._timerId2 = setTimeout(async () => {
                    await this.fetchClients({page: this.page2, search: this.customer_select});
                    
                    this.page2 = this.clients.current_page;
                    this.last_page2 = this.mavjud_products.last_page;
                    this.pageCount2 = Math.ceil(this.clients.total / 20);

                    this.existingCustomers = this.existingCustomers.concat(this.clients.data);
                }, 500)
            }
        },
        // ============= ADD_ITEMS END =============








        

        // ============= PRICE_FORMATTER START =============
        formatPrice(value, input) {
            if(value != ''){

                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);

                if(input == 'amount'){
                    this.sold.amount = currency
                }else if(input == 'price'){
                    this.sold.price = currency
                }else if(input == 'total_price'){
                    this.sold.total_price = currency
                }else if(input == 'paid'){
                    this.sold.paid = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============










        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.sold = {customer: '', product_id: '', amount: 0,
                price: 0, total_price: '', paid: 0}
            this.date = ''
            this.existingProduct = '';
            this.productSearch = '',
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeDialog(){
            this.clearInputs();
            this.$parent.soldDialog = false;
            this.$refs.formSellProduct.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============






        






        // ============= ON_SUBMIT2 START =============
        async onSubmit2(){
            this.$refs.formProductSelectReadyProducts.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============
                    this.$refs.formProductSelectReadyProducts.reset();
                    this.productSelectDialog = false;
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        },
        // ============= ON_SUBMIT2 END =============










        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formSellProduct.validate().then(async(success) => {
                if(success){
                    let client;
                    // ============= IF SUCCESS =============
                    if(this.customer_type == 'new'){
                        let newClient = await this.postClient(
                        { 
                            name: this.sold.customer,
                            phone_number: this.sold.phone_number
                        })
                        if(newClient.response && newClient.response.status == 422){
                            if(newClient.response.data.errors.name){
                                this.errorMessage = newClient.response.data.errors.name[0];
                                this.errorPlace = 'name'
                            }else if(newClient.response.data.errors.phone_number){
                                this.errorMessage = newClient.response.data.errors.phone_number[0];
                                this.errorPlace = 'phone_number'
                            }
                            this.errorStatus = true
                            this.$toast.error(this.errorMessage);
                        }else if(newClient.response && newClient.response.status == 409){
                            if(newClient.response.data.message){
                                this.errorMessage = newClient.response.data.message;
                            }
                            this.$toast.error(this.errorMessage);
                        }else{
                            client = newClient.id
                        }
                    }else if(this.customer_type == 'existing'){
                        client = this.existingCustomer
                    }


                    // ============= ADDING_NEW_SALE START =============
                    let sale = await this.postSales(
                        { 
                            created_at: this.date,
                            client_id: client,
                            product_id: parseInt(this.existingProduct.product_id),
                            amount: parseFloat(this.sold.amount.toString().replace(/,/g, '')),
                            price: parseFloat(this.sold.price.toString().replace(/,/g, '')),
                            total_price: parseFloat(this.sold.total_price.toString().replace(/,/g, '')),
                            paid: parseFloat(this.sold.paid.toString().replace(/,/g, ''))
                        })

                    if(sale.response && sale.response.status == 422){
                        if(sale.response.data.errors.customer){
                            this.errorMessage = sale.response.data.errors.customer[0];
                            this.errorPlace = 'customer'
                        }else if(sale.response.data.errors.amount){
                            this.errorMessage = sale.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }else if(sale.response.data.errors.price){
                            this.errorMessage = sale.response.data.errors.price[0];
                            this.errorPlace = 'price'
                        }else if(sale.response.data.errors.total_price){
                            this.errorMessage = sale.response.data.errors.total_price[0];
                            this.errorPlace = 'total_price'
                        }else if(sale.response.data.errors.paid){
                            this.errorMessage = sale.response.data.errors.paid[0];
                            this.errorPlace = 'paid'
                        }else if(sale.response.data.errors.client_id){
                            this.errorMessage = sale.response.data.errors.client_id[0];
                            this.errorPlace = 'name'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(sale.response && sale.response.status == 409){
                        if(sale.response.data.message){
                            this.errorMessage = sale.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        let toastTitle = 'Muvaffaqiyatli sotildi!'
                        this.$toast.success(toastTitle);
                        this.closeDialog();
                        await this.refresh();
                        await this.refresh2();
                        this.$store.commit('main/SET_UPDATE_READY_PRODUCTS_OUTCOME', true)
                        this.$store.commit('main/SET_UPDATE_READY_PRODUCTS_EXISTS', true)
                        
                    }
                    // ============= ADDING_NEW_SALE END =============
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT END =============

    }

}
</script>


<style>

</style>
