<template>
    <div>
        <v-data-table
            :headers="headers"
            :items="items"
            item-key="id"
            :loading="loading"
            disable-sort
            hide-default-footer
        >
            <!-- eslint-disable  -->
           
            <template v-slot:item.from_where="{item, index}">
                <v-menu 
                    v-model="menu[index]"
                    :close-on-content-click="false"
                    :nudge-width="220"
                    offset-x
                    offset-y>
                    <template v-slot:activator="{ on, attrs }">
                        <a class="d-flex" v-bind="attrs" v-on="on">
                            <span style="color: #2782F6;">{{item.from_where}}</span>
                        </a>
                    </template>
                    <v-list class="process">
                        <div class="process-title">
                            <span>Qo'shimcha ma'lumotlar</span>
                            <!-- <div class="d-flex aling-center cursor-pointer" @click="menu[index] = false">
                                <img src="@/assets/images/BackButton.svg" style="width: 30px;">
                            </div> -->
                        </div>

                        <div class="pa-1">Buyurtma soni: {{formatPrice(item.order.amount)}} ta</div>
                        <div class="pa-1">Buyurtma dona narxi: {{formatPrice(item.order.price)}} so`m</div>
                        <div class="pa-1">Buyurtma umumiy narxi: {{formatPrice(item.order.total_price)}} so`m</div>
                    </v-list>
                </v-menu>
            </template>

            <template v-slot:item.type="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.type }}</span>
                </div>
            </template>
            <template v-slot:item.name="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.name }}</span>
                </div>
            </template>
            <template v-slot:item.code="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">#{{ item.code }}</span>
                </div>
            </template>
            <template v-slot:item.amount="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.amount) }} ta</span>
                </div>
            </template>
            <template v-slot:item.date="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.date }}</span>
                </div>
            </template>

            <!-- <template v-slot:item.actions="{item}">
                <div class="d-flex align-center justify-center">
                    <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'ready_kirim')">
                        <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                    </div>
                    <div class="cursor-pointer mt-1" v-if="$can('delete', 'ready_kirim')">
                        <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                    </div>
                </div>
            </template> -->

            <template v-slot:no-data>
                <div>
                    <no-data class="mt-6"></no-data>
                    <br>
                    <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                </div>
            </template>

        </v-data-table>


        <v-pagination
            v-model="page"
            :length="pageCount"
            total-visible="7"
            class="my-2"
        ></v-pagination>

    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import NoData from "@/components/base/NoData.vue";
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin, loaderMixin],
    components: {
        DeleteModal,
        NoData
    },
    data() {
        return {
            menu: [false],
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            loading: false,
            headers: [
                // {text: 'Status', value: 'status', align: 'start', class: 'table-header'},
                {text: 'Qayerdan', value: 'from_where', align: 'start', class: 'table-header'},
                {text: 'Nomi', value: 'name', align: 'center', class: 'table-header'},
                {text: 'Kodi', value: 'code', align: 'center', class: 'table-header'},
                {text: 'Dona', value: 'amount', align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'date', align: 'center', class: 'table-header'},
                // {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: []
        }
    },
    async mounted(){
        await this.refresh()
    },
    watch:{
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.ready_products_income': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_READY_PRODUCTS_INCOME', false)
                }
            },
            deep: true,
            immediate: true
        },
        '$route.params.id': {
            handler: async function(value) {
                await this.refresh();
            },
            deep: true,
            immediate: true
        },
    },
    computed: {
        ...mapState({
            ready_order_income: state => state.readyProducts.ready_order_income,
            searchValue: state => state.main.searchValue,
            dateFilterValue: state => state.main.dateFilterValue
        })
    },
    methods: {
        ...mapActions({
            fetchReadyOrderIncome: 'readyProducts/fetchReadyOrderIncome',
        }),
        async refresh(){
            this.loading = true;
            await this.fetchReadyOrderIncome({page: this.page, category_id: this.$route.params.id, search: this.searchValue, date: this.dateFilterValue});
            this.page = this.ready_order_income.current_page;
            this.pageCount = Math.ceil(this.ready_order_income.total / 10);
            this.items = this.ready_order_income.data;
            this.items.forEach((elem, index) => {
                elem.from_where = elem.order.factory.name
                elem.name = elem.order.product.name;    
                elem.code = elem.order.product.code;
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY');
            })
            this.loading = false;
        },
        closeEditModal(){
            this.editModal = false
        },
    }
};
</script>

