<template>
    <div>
        <v-data-table
            :headers="headers"
            :items="items"
            item-key="id"
            :loading="loading"
            disable-sort
            hide-default-footer
        >
            <!-- eslint-disable  -->
            
            <template v-slot:item.customer="{item}">
                <div class="d-flex">
                    <span>{{ item.customer }}</span>
                </div>
            </template>
            
            <template v-slot:item.name="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.name }}</span>
                </div>
            </template>

            <template v-slot:item.color="{item}">
                <div class="d-flex justify-center">
                    <!-- <div class="small_circle"></div> -->
                    <span class="text-black">{{ item.color }}</span>
                </div>
            </template>
            
            <template v-slot:item.amount="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.amount) }} ta</span>
                </div>
            </template>

            <template v-slot:item.price="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.price) }} so`m</span>
                </div>
            </template>

            <template v-slot:item.total_price="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.total_price) }} so`m</span>
                </div>
            </template>

            <template v-slot:item.date="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.date }}</span>
                </div>
            </template>

            <template v-slot:item.actions="{item}">
                <div class="d-flex align-center justify-center">
                    <div class="cursor-pointer mr-2 mt-1" 
                    @click="(product = item) && (editModal = true )"
                    v-if="$can('update', 'ready_chiqim')">
                        <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                    </div>
                    <!-- <div class="cursor-pointer mt-1"
                    @click="(element = item) && (deleteModal = true)"
                    v-if="$can('delete', 'ready_chiqim')">
                        <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                    </div> -->
                </div>
            </template>

            <template v-slot:no-data>
                <div>
                    <no-data class="mt-6"></no-data>
                    <br>
                    <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                </div>
            </template>

        </v-data-table>


        <v-pagination
            v-model="page"
            :length="pageCount"
            total-visible="7"
            class="my-2"
        ></v-pagination>




        <v-dialog
            max-width="400"
            persistent
            v-model="editModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Mahsulot tahriri</span>
                    <v-btn icon @click="closeEditDialog">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                
                <ValidationObserver ref="formEditOutcomeReadyProducts" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">
                      

                        <div class="mt-5">
                            <span class="text-black ml-1">Massa</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'amount']}"
                              v-slot="{ errors }">
                                <v-text-field outlined :value="formatPrice(product.amount)" class="mt-2"
                                    @input="formatPrice2($event, 'amount')"
                                    placeholder="Misol: 200 kg" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>


                        <div class="mt-5">
                            <span class="text-black ml-1">KG narxi</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'price']}"
                              v-slot="{ errors }">
                                <v-text-field outlined :value="formatPrice(product.price)" class="mt-2"
                                    @input="formatPrice2($event, 'price')"
                                    placeholder="Misol: 1,000 so`m" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>


                        <div class="mt-5">
                            <span class="text-black ml-1">Umumiy summa</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'total_price']}"
                              v-slot="{ errors }">
                                <v-text-field outlined :value="getTotalPrice" class="mt-2"
                                    @input="formatPrice2($event, 'total_price')"
                                    placeholder="Misol: 100,000 so`m" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeEditDialog"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tahrirlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>



        <!-- <DeleteModal 
        :deleteModal="deleteModal" 
        :user_id="{id: element.id, type: 'accessory'}" 
        :actionName="'warehouse/deleteProductIncome'"
        :modalTitle="'Aksessuar ochirilishi'"
        :modalDesc="'Bu aksessuarni ochirmoqchimisiz?'"></DeleteModal> -->
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import NoData from "@/components/base/NoData.vue";
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin, loaderMixin],
    components: {
        DeleteModal,
        NoData
    },
    data() {
        return {
            // ============= SECONDARY_DATA =============
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            headers: [
                // {text: 'Status', value: 'status', align: 'start', class: 'table-header'},
                {text: 'Qayerga', value: 'customer', align: 'start', class: 'table-header'},
                {text: 'Nomi', value: 'name', align: 'center', class: 'table-header'},
                {text: 'Dona', value: 'amount', align: 'center', class: 'table-header'},
                {text: 'Dona narxi', value: 'price', align: 'center', class: 'table-header'},
                {text: 'Summasi', value: 'total_price', align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'date', align: 'center', class: 'table-header'},
                {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: [],
            element: {},
            product: {
                amount: '',
                price: '',
                total_price: '',
            },
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted(){
        await this.refresh();

        if(!this.$can('update', 'ready_chiqim')){
            this.headers.pop();
        }
    },
    watch:{
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.ready_products_outcome': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_READY_PRODUCTS_OUTCOME', false)
                }
            },
            deep: true,
            immediate: true
        },
        '$route.params.id': {
            handler: async function(value) {
                await this.refresh();
            },
            deep: true,
            immediate: true
        },
    },
    computed: {
        ...mapState({
            sales: state => state.readyProducts.sales,
            searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
        }),

        // ============= CALCULATE_TOTAL_PRICE START =============
        getTotalPrice() {
            let price = this.product.price.toString().replace(/,/g, '')
            let amount = this.product.amount.toString().replace(/,/g, '');

            let result = price * amount
            
            return  this.product.total_price = new Intl.NumberFormat('en-US').format(result)
        }
        // ============= CALCULATE_TOTAL_PRICE END =============
    },
    methods: {
        ...mapActions({
            fetchSales: 'readyProducts/fetchSales',
            updateSales: 'updateDelete/updateSales'
        }),
        async refresh(){
            this.loading = true;
            await this.fetchSales({page: this.page, category_id: this.$route.params.id, search: this.searchValue, date: this.dateFilterValue});
            this.page = this.sales.current_page;
            this.pageCount = Math.ceil(this.sales.total / 10);
            this.items = this.sales.data;
            this.items.forEach((elem) => {
                elem.name = elem.product.name;    
                elem.code = elem.product.code;
                elem.customer = elem.client.name
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY');
            })
            this.loading = false;
        },
        closeEditModal(){
            this.editModal = false
        },







        // ============= PRICE_FORMATTER START =============
        formatPrice2(value, input) {
            if(value != ''){
                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'amount'){
                    this.product.amount = currency
                }else if(input == 'price'){
                    this.product.price = currency
                }else if(input == 'total_price'){
                    this.product.total_price = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============










        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.product = {
                amount: '',
                price: '',
                total_price: '',
            }
            this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeEditDialog(){
            this.clearInputs();
            this.editModal = false;
            this.$refs.formEditOutcomeReadyProducts.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============















        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formEditOutcomeReadyProducts.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============


                    // ============= EDITING_IP START =============
                    let ip = await this.updateSales(
                        { 
                            id: this.product.id,
                            amount: parseFloat(this.product.amount.toString().replace(/,/g, '')),
                            price: parseFloat(this.product.price.toString().replace(/,/g, '')),
                            total_price: parseFloat(this.product.total_price.toString().replace(/,/g, '')),
                        })

                    if(ip.response && ip.response.status == 422){
                        if(ip.response.data.errors.amount){
                            this.errorMessage = ip.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }else if(ip.response.data.errors.price){
                            this.errorMessage = ip.response.data.errors.price[0];
                            this.errorPlace = 'price'
                        }else if(ip.response.data.errors.total_price){
                            this.errorMessage = ip.response.data.errors.total_price[0];
                            this.errorPlace = 'total_price'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(ip.response && ip.response.status == 409){
                        if(ip.response.data.message){
                            this.errorMessage = ip.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        let toastTitle = 'Muvaffaqiyatli tahrirlandi!'
                        this.$toast.success(toastTitle);
                        this.closeEditDialog();
                        this.refresh()
                        // this.$store.commit('main/SET_UPDATE_MAVJUD_IPS', true)
                    }
                    // ============= EDITING_IP END =============
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })
        }
        // ============= ON_SUBMIT END =============
    }
};
</script>

<style lang="scss">

</style>
