<template>
    <div class="ready-products" id="ready-products_id">
        <div class="tabs">
            <v-tabs
                v-model="tab"
                color="black"
            >
                <v-tabs-slider color="tab-slider-color"></v-tabs-slider>

                <v-tab href="#tab-1" @click="routeNAME = 'ready-products-kirim'" v-if="$can('read', 'ready_kirim')">
                    Kirim
                </v-tab>

                <v-tab href="#tab-2" @click="routeNAME = 'ready-products-chiqim'" v-if="$can('read', 'ready_chiqim')">
                    Chiqim
                </v-tab>

                <v-tab href="#tab-3" @click="routeNAME = 'ready-products-mavjud'" v-if="$can('read', 'ready_mavjud')">
                    Mavjud mahsulotlar
                </v-tab>
            </v-tabs>
            <v-col cols="auto">
                <div class="d-flex align-center">
                    <v-btn dark elevation="0" class="mr-2" v-if="$can('read', 'ready_sotildi')"
                    style="background: #2886FF;" @click="soldDialog = true">
                        <img src="@/assets/icons/money-send.svg" alt="money-send" class="mr-1" width="22"> 
                        <span class="fs-16px" style="text-transform: none;">Sotildi</span>
                    </v-btn>
                    <!-- <TableFilter :filterMenu="filterMenu"></TableFilter> -->
                    <DatePicker></DatePicker>
                </div>
            </v-col>
        </div>
        <v-divider></v-divider>
        <v-tabs-items v-model="tab" class="tab-items">
            <v-tab-item value="tab-1">
                <Income v-if="$can('read', 'ready_kirim')"></Income>
            </v-tab-item>
            
            <v-tab-item value="tab-2">
               <Outcome v-if="$can('read', 'ready_chiqim')"></Outcome>
            </v-tab-item>

            <v-tab-item value="tab-3">
                <ExistedProducts v-if="$can('read', 'ready_mavjud')"></ExistedProducts>
            </v-tab-item>
        </v-tabs-items>
        <Sold :soldDialog="soldDialog" v-if="$can('read', 'ready_sotildi')"></Sold>
    </div>
</template>

<script>
import Income from './Income.vue'
import Outcome from './Outcome.vue'
import ExistedProducts from './ExistedProducts.vue'
import Sold from '@/components/modals/ReadyProducts/Sold.vue'
//import TableFilter from '@/components/TableFilter.vue'
import DatePicker from '@/components/DatePicker.vue'

export default {
    components: {
        Income,
        Outcome,
        ExistedProducts,
        Sold,
        DatePicker
        // TableFilter
    },
    data() {
        return {
            soldDialog: false,
            tab: '',
            filterMenu: false,
        }
    },
    computed: {
        routeNAME: {
            get: function() {
                return this.$store.state.main.routeNAME
            },
            set: function(newValue) {
                return this.$store.state.main.routeNAME = newValue
            }
        },
    }
};
</script>

